//SCSS Footer

.text-footer{
  padding: 30px 0 30px 0;
  text-align: center;
  color: $white;
  font-size: 23px;
  font-family: robot-cond;
}

.footer-font-awsome{
  background-color: $brown;
}

.content-fa{
  text-align: center;
  padding-bottom: 30px;
}
.fa-facebook-f{
  color: $white;
  font-size: 40px;
  margin-right: 75px;
  padding-top: 10px;


}
.fa-twitter{
  color: $white;
  font-size: 40px;
  margin-right: 150px;
  margin-left: 75px;

}

.fa-instagram{
  color: $white;
  font-size: 40px;

}
.content-link-footer{
  @include flex(center, center);
  background-color: rgba(198, 193, 152,0.5);

}

.link-footer{
  padding: 75px;
  font-family: robot-cond;
}
.link-footer:hover{
   text-decoration: underline;
  color: $black;
}

.content-counter{
  border-top: 3px solid $black;

}

.counter_views{
  text-align: center;
  background-color: rgba(198, 193, 152,0.5);
  padding: 7px 0;
  font-family: robot-cond;
}

.font_weight_counter>span{
  font-weight: 100;
}


.copyright{
  border-top: 3px solid $black;
  border-bottom: 3px solid $black;
  background-color: $green-o;
}

.text-copyright{
  text-align: center;
  padding-top: 15px;
  font-family: robot-cond;
}

.text-copyright-m{
  margin-bottom: 15px;
}


.footer-end{
  background-color: $green-o;

  padding: 15px;
}

.footer-end2{
  background-color: $brown;
  padding: 25px;
}

//Media query

@media screen and (max-width: 767px) {
  .content-link-footer{
    flex-direction: column;
  }
  .link-footer{
    padding: 15px 0px;
    width: auto;

  }
}

@media screen and (max-width: 320px){
  .text-footer {
    font-size: 20px
  }

  .fa-facebook-f{
    margin-right: -14px;
  }

  .fa-twitter {
    margin-right: 60px;
  }

  .fa-facebook-f, .fa-twitter, .fa-instagram{
    font-size: 50px;
  }
}